// import { document } from 'postcss/lib/postcss'
import './index.html'
import './index.scss'
const header = document.querySelector('.header')
const contentHeader = document.querySelector('.header__content')
const burger = document.getElementById('menu-toggle')
const navHeader = document.querySelector('.header__nav')
const buttonHeader = document.querySelector('.header__button')
const popupLinks = document.querySelectorAll('.popup__link')
const popupCloseElements = document.querySelectorAll('.popup__close')

burger.addEventListener('click', function (e){
	let burgerStatus = burger.checked
	if(burgerStatus === true){
		header.classList.add('open')
		contentHeader.classList.add('open')
		navHeader.classList.add('open')
		buttonHeader.classList.add('open')
	} else{
		buttonHeader.classList.remove('open')
		header.classList.remove('open')
		contentHeader.classList.remove('open')
		navHeader.classList.remove('open')
	}
})

openBurger(burger, burgerStatus)

const scrollController = {
	scrollPosition: 0,
	resize: window.innerWidth - document.body.offsetWidth,
	scrolLock(){
		scrollController.scrollPosition = window.scrollY;
		header.style.cssText =`
		padding-right: ${scrollController.resize}px;
		`
		document.body.style.cssText = `
		overflow: hidden;
		position: fixed;
		top: -${scrollController.scrollPosition}px;
		left: 0;
		height: 100vh;
		width: 100vw;
		padding-right: ${window.innerWidth - document.body.offsetWidth}px;
		`
		document.documentElement.style.scrollBehavior = 'unset';
	},
	scrollUnlock(){
		header.style.cssText ='';
		document.body.style.cssText = '';
		window.scroll({top: scrollController.scrollPosition})
		document.documentElement.style.scrollBehavior = ''
	},
}
if (popupLinks.length > 0) {
	for (let index = 0; index < popupLinks.length; index++) {
		const popupLink = popupLinks[index];
		popupLink.addEventListener('click', function (e){
			const currentPopup = document.getElementById('popup')
			popupOpen(currentPopup)
			e.preventDefault()
		})
	}
}
if(popupCloseElements.length > 0){
	for (let index = 0; index < popupCloseElements.length; index++) {
		const closeElement = popupCloseElements[index];
		closeElement.addEventListener('click', function (e) {
			popupClose(closeElement.closest('.popup'))
			e.preventDefault()
		})
	}
}

function popupOpen(currentPopup) {
		currentPopup.classList.add('open')
		currentPopup.addEventListener('click', function (e){
			if(!e.target.closest('.popup__form')){
				popupClose(e.target.closest('.popup'))
			}
		})
		scrollController.scrolLock()
}

function popupClose(popupActive) {
	popupActive.classList.remove('open')
	scrollController.scrollUnlock()
}