  align-items: center;
  justify-content: center;
  background: #085ea5;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 5px;
}

.contact__list-image {
  max-width: 50px;
  max-height: 50px;
}

.contact__list-text {
  font-size: 20px;
  font-family: "Arial";
  color: rgb(62, 64, 60);
  font-weight: bold;
  line-height: 2.133;
  text-align: left;
}

@media (min-width: 1080.02px) {
  .contacts {
    padding: 40px 0px;
  }
  .contact__list {
    justify-content: space-around;
  }
  .contact__list-item {
    margin: 0px 50px 10px 50px;
  }
  .contact__tittle {
    margin-bottom: 50px;
    font-size: 43px;
  }
}
@media (max-width: 844.98px) {
  .contact__list-item {
    margin-bottom: 10px;
  }
  .contact__list-item:last-child {
    margin-bottom: 0px;
  }
}
@media (max-width: 439.98px) {
  .contact__list {
    justify-content: center;
    width: 100%;
  }
  .contact__list-box {
    width: 25px;
    height: 25px;
    margin-right: 5px;
  }
  .contact__list-image {
    max-width: 14px;
    max-height: 20px;
  }
  .contact__list-text {
    font-size: 12px;
  }
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.8);
  visibility: hidden;
  transition: all 0.8s ease 0s;
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .popup__wrapper {
  transform: translate(0px, 0px);
  opacity: 1;
}

.popup__wrapper {
  position: relative;
  max-width: 500px;
  margin: 0px auto;
  background: #d3d3d3;
  padding: 70px 50px;
  opacity: 0;
  transform: translate(0px, -100%);
  transition: all 0.8s ease 0s;
}

.popup__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup__form-tittle {
  font-family: "ArialNarrow";
  font-size: 45px;
  text-transform: uppercase;
  color: #2f3880;
  margin-bottom: 20px;
}

.popup__form-subtittle {
  font-family: "ArialNarrow";
  font-size: 25px;
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
}

.popup__form-input {
  margin-top: 15px;
  border: 1px solid black;
  padding: 10px 25px;
  box-shadow: none;
}

.popup__form-input:last-child {
  margin-bottom: 15px;
}

.popup__form-input::-moz-placeholder {
  color: black;
}

.popup__form-input::placeholder {
  color: black;
}

.popup__form-sumbit {
  font-family: "ArialNarrow";
  font-weight: bold;
  font-size: 30px;
  text-transform: uppercase;
  color: white;
  background: #009de0;
  border: 1px solid black;
  padding: 0px;
  text-align: center;
  padding: 10px;
  width: 100%;
  box-shadow: none;
}

.popup__close {
  font-family: "ArialNarrow";
  position: absolute;
  top: 10px;
  right: 30px;
  font-size: 30px;
  cursor: pointer;
}

@media (max-width: 599.98px) {
  .popup__wrapper {
    max-width: 500px;
    padding: 50px 35px;
  }
  .popup__form-tittle {
    font-size: 27px;
    margin-bottom: 15px;
  }
  .popup__form-subtittle {
    font-size: 15px;
  }
  .popup__form-input {
    margin-top: 10px;
    padding: 5px 20px;
  }
  .popup__form-input:last-child {
    margin-bottom: 10px;
  }
  .popup__form-sumbit {
    font-size: 20px;
    padding: 5px;
  }
  .popup__close {
    top: 10px;
    right: 20px;
    font-size: 20px;
  }
}
.footer {
  padding: 20px 0px;
  background: #d9dde3;
  height: 100%;
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.footer__logo {
  max-width: 111px;
  max-height: 41px;
  margin-right: 10px;
}

.footer__logo-link {
  display: inline-block;
  transition: all 0.4s ease-in;
}

.footer__logo:hover {
  transform: scale(1.1);
}

.logo {
  width: 100%;
  height: 100%;
}

.copyright__company {
  font-size: 14px;
  font-family: "ArialNarrow";
  color: rgb(62, 64, 60);
  line-height: 0.987;
  text-align: left;
  text-wrap: nowrap;
}

.copyright__company-name {
  position: relative;
  margin-right: 5px;
}

.copyright__company-name::before {
  content: "";
  position: absolute;
  top: 0;
  right: -5px;
  width: 1px;
  height: 100%;
  background: rgb(46, 48, 45);
}

.copyright__agreement-link {
  display: inline-block;
  font-size: 12px;
  font-family: "ArialNarrow";
  color: rgb(62, 64, 60);
  line-height: 0.8;
  text-align: left;
  border-bottom: 1px rgb(62, 64, 60) solid;
  transition: all 0.4s ease-in;
}

.copyright__agreement-link:hover {
  transform: scale(1.1);
}

@media (min-width: 1080.02px) {
  .footer__logo {
    max-width: 200px;
    max-height: 100%;
  }
  .copyright__company {
    font-size: 20px;
  }
  .copyright__company-name::before {
    width: 1px;
  }
  .copyright__agreement-link {
    font-size: 16px;
  }
}
@media (max-width: 439.98px) {
  .copyright__company {
    font-size: 10px;
  }
  .copyright__agreement-link {
    font-size: 9px;
  }
