\"), url(fonts/arialnarrow.woff2) format(\"woff2\"), url(fonts/arialnarrow.woff) format(\"woff\"), url(fonts/arialnarrow.eot) format(\"eot\");\n}\n/**\n  Нормализация блочной модели\n */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n/**\n\tУбираем внутренние отступы слева тегам списков,\n\tу которых есть атрибут class\n\t*/\n:where(ul, ol):where([class]) {\n  padding-left: 0;\n}\n\n/**\n\tУбираем внешние отступы body и двум другим тегам,\n\tу которых есть атрибут class\n\t*/\nbody,\n:where(blockquote, figure):where([class]) {\n  margin: 0;\n}\n\n/**\n\tУбираем внешние отступы вертикали нужным тегам,\n\tу которых есть атрибут class\n\t*/\n:where(h1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nul,\nol,\ndl):where([class]) {\n  margin-block: 0;\n}\n\n:where(dd[class]) {\n  margin-left: 0;\n}\n\n:where(fieldset[class]) {\n  margin-left: 0;\n  padding: 0;\n  border: none;\n}\n\n/**\n\tУбираем стандартный маркер маркированному списку,\n\tу которого есть атрибут class\n\t*/\n:where(ul[class]) {\n  list-style: none;\n}\n\n/**\n\tОбнуляем вертикальные внешние отступы параграфа,\n\tобъявляем локальную переменную для внешнего отступа вниз,\n\tчтобы избежать взаимодействие с более сложным селектором\n\t*/\np {\n  --paragraphMarginBottom: 24px;\n  margin-block: 0;\n}\n\n/**\n\tВнешний отступ вниз для параграфа без атрибута class,\n\tкоторый расположен не последним среди своих соседних элементов\n\t*/\np:where(:not([class]):not(:last-child)) {\n  margin-bottom: var(--paragraphMarginBottom);\n}\n\n/**\n\tУпрощаем работу с изображениями\n\t*/\nimg {\n  display: block;\n  max-width: 100%;\n}\n\n/**\n\tНаследуем свойства шрифт для полей ввода\n\t*/\ninput,\ntextarea,\nselect,\nbutton {\n  font: inherit;\n}\n\nhtml {\n  /**\n  \tПригодится в большинстве ситуаций\n  \t(когда, например, нужно будет \"прижать\" футер к низу сайта)\n  \t*/\n  height: 100%;\n}\n\n/**\n\tПлавный скролл\n\t*/\nhtml,\n:has(:target) {\n  scroll-behavior: smooth;\n}\n\nbody {\n  /**\n  \tПригодится в большинстве ситуаций\n  \t(когда, например, нужно будет \"прижать\" футер к низу сайта)\n  \t*/\n  min-height: 100%;\n  /**\n  \tУнифицированный интерлиньяж\n  \t*/\n  line-height: 1.5;\n}\n\n/**\n\tПриводим к единому цвету svg-элементы\n\t*/\nsvg *[fill] {\n  fill: currentColor;\n}\n\nsvg *[stroke] {\n  stroke: currentColor;\n}\n\n/**\n\tЧиним баг задержки смены цвета при взаимодействии с svg-элементами\n\t*/\nsvg * {\n  transition-property: fill, stroke;\n}\n\n/**\n\tУдаляем все анимации и переходы для людей,\n\tкоторые предпочитают их не использовать\n\t*/\n@media (prefers-reduced-motion: reduce) {\n  * {\n    animation-duration: 0.01ms !important;\n    animation-iteration-count: 1 !important;\n    transition-duration: 0.01ms !important;\n  }\n}\n/**\n\tУбираем стандартные эффекты ссылок при наведении и посещении,\n\tа так же цвет\n*/\na,\na:visited,\na:hover {\n  text-decoration: none;\n}\n\na {\n  color: inherit;\n}\n\nhtml {\n  scroll-padding-top: 86px;\n}\n\n.warrper {\n  min-height: 100%;\n  width: 100%;\n  overflow: hidden;\n}\n\n.container {\n  height: 100%;\n  max-width: 1050px;\n  margin: 0px auto;\n  padding: 0px 15px;\n}\n\n.tittle {\n  font-size: 43px;\n  font-family: \"DAMN\";\n  text-transform: uppercase;\n  line-height: 1.5;\n}\n\n.contact__map {\n  width: 100%;\n  height: 500px;\n}\n\n@media (width > 1080px) {\n  .container {\n    height: 100%;\n    max-width: 1440px;\n    margin: 0px auto;\n    padding: 0px 15px;\n  }\n}\n@media (width < 440px) {\n  .container {\n    padding: 0px 5px;\n  }\n  .tittle {\n    font-size: 23px;\n  }\n  .contact__map {\n    width: 100%;\n    height: 200px;\n  }\n}\n.header {\n  background: #d9dde3;\n  position: fixed;\n  top: 0;\n  width: 100%;\n  z-index: 99;\n  height: 100px;\n}\n\n.header__content {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  height: 100%;\n  padding: 10px 0px;\n}\n\n.header__logo {\n  width: 100%;\n  max-width: 200px;\n  margin-right: 10px;\n  transition: all 0.4s ease-i